*{
overflow: auto;
}
.fab{
position: absolute;
    right: 20px;
    bottom: 20px;
}
.App {
  font-family: sans-serif;
    background-color: #eee;
    background-image: linear-gradient(to top right, #DEA890, #E89ABB,#CA9DD6);
height : 100%;
    background-size: 100% 100%;
}
.addtodo {
margin-bottom: 0.5rem;
padding: 0.5rem;
background-color: white;
}
.textfielddiv {
  padding-right: 0.5rem;
}
.todo {
  display: flex;
  flex-direction: row;
  padding: 1rem;
  margin-top: 0.5rem;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  align-items: center;
}
.info {
  width: 85%;
}
.control{
width: 15%;
display: flex;
justify-content: flex-end;
}
#root, html,body{
height : 100%;
margin: 0;
}
